import Vue from 'vue'
import Router from 'vue-router'
import zarapa from './components/zarapa.vue'
import principal from './components/principal'
import principales from './components/principales'
import segundarios from './components/segundarios'
import exit from './components/exit'
import tipos from './components/tiposdat'
import creditos from './components/creditos'
import presentacion from './components/presentacion'
import bebidas from './components/bebidas'


Vue.use(Router)

export default new Router({
  mode: 'history',
  
  base: process.env.BASE_URL,
  routes: [
           
     {
      path: '/',
      name: 'principal',
      component: principal,

      children:[

        {
          path: '/zarapa',
          name: 'zarapa',
          component: zarapa,
          meta: {title: '¿Que es una Zarapa?'}
        },
  
      {
          path: '/principales',
          name: 'principales',
          component: principales,
          meta: { title: 'Platos Principales' }
      },
      {
        path: '/segundarios',
        name: 'segundarios',
        component: segundarios,
        meta: { title: 'Platos Segundarios' }
      },
      
      {
        path: '/bebidas',
        name: 'bebidas',
        component: bebidas,
        meta: { title: 'Bebidas' }
      },
      
      {
        path: '/exit',
        name: 'exit',
        component: exit,
      },
      {
        path: '/tiposdat',
        name: 'tipos',
        component: tipos,
      },
      {
        path: '/creditos',
        name: 'creditos',
        component: creditos,
      },
      {
        path: '/',
        name: 'Presentacion',
        component: presentacion,
        meta: { title: 'Inicio' },
      },
    ]}
]
})