<template>  

  <div class="principal" >
   <v-toolbar
      expanded
      color="brown"
      dark
      height="90px"

      class="barups"
    >
      <v-app-bar-nav-icon @click="drawer = !drawer" ></v-app-bar-nav-icon>

       <v-spacer></v-spacer>

      <v-toolbar-title style="font-size: 25px;"class="backt">{{ titulo }}</v-toolbar-title>
      <v-spacer></v-spacer>
    </v-toolbar>

<v-row > 
    <v-navigation-drawer
      class="menu"
      style="background-color: rgba(63,48,44)"
      absolute
      v-model="drawer"
      temporary
      dark
      left
      height="85%"
      width="100%"
    >
      <v-list nav dance>
        <v-list-item
        
          v-for="item in items"
          :key="item.title"
          :to = "item.ruta"
          link
        >
          <v-list-item-icon>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title class="titulosnav">{{ item.title }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>

      <template v-slot:append>
        <div class="pa-2">
          <v-btn @click="alert = true" > SALIR <v-icon right>mdi-exit-run</v-icon></v-btn>

         

        </div>
      </template>
    </v-navigation-drawer>
</v-row>



  <v-row>
  <v-container >

  <v-dialog
  class="alertexit" 
  v-model="alert"
  width="400px"
  height="">

    <v-card>   
      <v-toolbar> <v-spacer></v-spacer><v-toolbar-title> GRACIAS POR USAR NUESTRO SISTEMA </v-toolbar-title><v-spacer></v-spacer> </v-toolbar>
      
      <v-spacer></v-spacer><v-card-title> ¿Como calificaria su esperiencia? </v-card-title><v-spacer></v-spacer>


     <v-card-text>
    
      <p><v-btn @click="calificacion = 1" class="ma-2" text color="red" icon>
      <v-icon color="red">mdi-heart</v-icon></v-btn> 

      <v-btn @click="calificacion = 2" class="ma-2" text icon color="blue">
      <v-icon>mdi-thumb-up</v-icon></v-btn>

      <v-btn @click="calificacion = 3" class="ma-2" text icon color="brown">
      <v-icon >mdi-thumb-down</v-icon></v-btn></p>

      <p >{{calificacionf}}</p>

      </v-card-text>


       <v-divider></v-divider>

      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn to="/" @click="alert = false">CERRAR</v-btn>
        <v-spacer></v-spacer>
        </v-card-actions>

    </v-card>
   
  </v-dialog>

      <div>
          <router-view  ></router-view>     
      </div>
  </v-container>
  </v-row>
   
  </div>

</template>

<script>
import Creditos from './creditos.vue'
import presentacion from './presentacion.vue'
export default {
    data() {
        return {
            titulo:"",
            drawer: false,
            alert: false,
            calificacion: "",
            items: [
                { title: "Inicio", icon: "mdi-home", ruta: "/" },
                { title: "¿Que es una Zarapa?", icon: "mdi-border-color", ruta: "zarapa" },
                { title: "Platos principales", icon: "mdi-file-document", ruta: "principales" },
                { title: "Platos segundarios", icon: "mdi-border-color", ruta: "segundarios" },
                { title: "Bebidas", icon: "mdi-ab-testing", ruta: "bebidas" },
                { title: "Creditos", icon: "mdi-account-multiple-outline", ruta: "creditos" },
            ],
             icons: [
                'mdi-facebook',
                'mdi-twitter',
                'mdi-linkedin',
                'mdi-instagram',
                      ],

        };
    },

    computed:{
      calificacionf(){
        if (this.calificacion == "")
        return "";
        if (this.calificacion == 1 )
        return 'Nos alegra demasiado, hasta la proxima!';
        if (this.calificacion == 2)
        return 'Gracias! Mejoraremos para gustarte mas.';
        if (this.calificacion == 3)
        return 'Que lastima! Seguiremos trabajando para mejorar.';
      },

      exit(){
        swal("Hola estas feo")
      }     

    },

    watch: {
    $route(to) {
      if (to.meta && to.meta.title) {
        this.titulo = to.meta.title;
      }
    }
  },

    mounted(){
      if (this.$route.meta && this.$route.meta.title) {
      this.titulo = this.$route.meta.title;
    }

    components: { Creditos, presentacion }
}
}
</script>

